import request from '@/utils/request'
import taskModuleRequest from '@/utils/taskModuleRequest'
//登录
export function auth(data){
    return request({
      url: '/api/v2/permission/auth',
      method: 'post',
			data
    })
  }

//---------------------------------分组管理---------------------------------------------
//分组管理  获取分组
export function deviceGroups(data){
    return request({
      url: '/api/v2/groups?page=' + data.page + '&size=' + data.rows,
      method: 'get',
    })
  }
	
//分组管理  添加分组
export function addDeviceGroups(data){
    return request({
      url: '/api/v2/groups',
      method: 'post',
			data
    })
  }
	
	//分组管理  修改分组
	export function updataDeviceGroups(data){
	    return request({
	      url: '/api/v2/groups/' + data.groupId,
	      method: 'post',
				data
	    })
	  }
		
//分组管理  删除分组
export function delGroup(data){
    return request({
      url: '/api/v2/groups',
      method: 'delete',
			data
    })
  }
	
//分组管理  设备添加到分组
export function add2Groups(data){
    return request({
      url: '/api/v2/devices/to_group',
      method: 'post',
			data
    })
  }
//---------------------------------设备管理---------------------------------------------
//设备管理  设备
export function vDeviceses(data){
    return request({
      url: '/api/v2/devices?' + (data.groupName?('groupName=' + data.groupName + '&'):'') + (data.identifiers?('identifiers=' + data.identifiers+ '&'):'') + 'page=' + data.page + '&size=' + data.rows,
      method: 'get'
    })
  }



//---------------------------------任务模板---------------------------------------------
//任务模板  
export function taskTemplates(data){
    return request({
      url: '/api/v3/tasks/template?' + (data.name?('name=' + data.name+ '&'):'') + 'page=' + data.page + '&size=' + data.rows,
      method: 'get'
    })
  }
	
