//-----------------------------附件管理------------------------------

//附件列表  
export function attachments(data){
    return request({
      url: '/api/v2/attachments?' + 'page=' + data.page + '&size=' + data.rows,
      method: 'get'
    })
  }
	
	//附件 上传
	export function addAttachments(id,data){
	    return taskModuleRequest({
	      url: '/api/v2/attachments' + (id?('/' + id):''),
	      method: 'post',
				data
	    })
	  }

	//附件 删除
	export function delAttachments(id){
	    return request({
	      url: '/api/v2/attachments/' + id,
	      method: 'delete'
	    })
	  }
		
		//附件 下载地址
		export function addressAttachments(id){
		    return request({
		      url: '/api/v2/attachments/' + id,
		      method: 'get'
		    })
		  }